.guidecontainer {
    margin: auto;
    max-width: 650px;
    width: 90%;
}
.problemssection {
    background: #84c4cc;
    padding: 50px 0px;
}
.problemssection h3 {
    color: #fff;
    text-align: center;
    letter-spacing: 0px;
    font-size: 21pt;
    line-height: 28pt;
    font-weight: bold;
    margin: 0;
}
.problemssection .shopbtn {
    margin-top: 20px;
    text-align: center;
}
.shopbtn a {
    color: #fff !important;
    border: 1.5px solid #fff;
    padding: 10px 30px;
    text-transform: uppercase;
    font-size: 18px;
    display: inline-block;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    text-decoration: none;
}