.guidecontainer {
    margin: auto;
    max-width: 650px;
    width: 90%;
}
.guidecaption {
    color: #213945;
    text-align: center;
    font-size: 11pt;
    margin-top: 5px;
}
.airmicromodalsection {
    background: #d5f2f5;
    padding: 50px 0px;
}
.airmicromodalsection h3 {
    color: #213843;
    text-align: center;
    font-weight: bold;
    font-size: 30pt;
    letter-spacing: 0;
    margin-bottom: 35px;
}
.airmicromodalsection p.airhead {
    color: #213945;
    font-size: 13pt;
    text-align: right;
    line-height: 20pt;
}
.airmicromodalsection p {
    color: #213945;
    font-size: 11pt;
    text-align: right;
    line-height: 18pt;
}
.fabricmobileview {
    display: none;
}
img {
    max-width: 100%;
    image-rendering: auto;
}
img {
    vertical-align: middle;
    border-style: none;
}
@media (max-width: 1024px){
    .fabricdesktopview {
        display: none;
    }
    .fabricmobileview {
        display: block;
    }
    .expbottom  p {
        text-align: center;
    }
    }