.fabricguidelightblue {
    background: #edf9fa;
    padding: 50px 0px;
}
.guidecontainer {
    margin: auto;
    max-width: 650px;
    width: 90%;
}
.experiencetitle .exptitle1 {
    color: #213843;
    text-align: center;
    font-weight: bold;
    font-size: 30pt;
    line-height: 30pt;
    letter-spacing: 0;
    margin-bottom: 6px;
}
.expbottom .modalcontent p {
    color: #213945;
    font-size: 13pt;
    line-height: 20pt;
    text-align: right;
}
.experiencetitle .exptitle2 {
    color: #fff;
    background: #84c4cc;
    text-align: center;
    font-weight: bold;
    font-size: 30pt;
    line-height: 30pt;
    letter-spacing: 0;
    margin: 0 auto 6px auto;
    padding: 5px 15px;
    display: table;
}
.expbottom {
    margin-top: 20px;
    margin-bottom: 20px;
}
.modalcontent {
    margin-top: 20px;
}
.expbottom  p {
    color: #213945;
    font-size: 13pt;
    line-height: 20pt;
    text-align: right;
    margin-top: 20px;
}
.innerbottom .modalcontent p {
    color: #213945;
    font-size: 11pt;
    margin: 0;
    line-height: 18pt;
    text-align: left;
}
.fabricinnersection1 .innerbottom .modalcontent p {
    text-align: right;
}
img {
    max-width: 100%;
    image-rendering: auto;
}
.fabricinnersection {
    padding: 20px 0px;
}
.innerheader {
    background: #84c4cc;
    padding: 10px 20px;
}

.innerheader h3 {
    margin: 0;
    color: #fff;
    letter-spacing: 0;
    font-weight: 500;
    font-size: 17pt;
    line-height: 22pt;
}
.fabricmobileview {
    display: none;
}
.innermodalcircle {
    margin-top: -80px;
}
.guidecaption {
    color: #213945;
    text-align: center;
    font-size: 11pt;
    margin-top: 5px;
}
.fabricinnersection1 .innerbottom .modalcontent1 p {
    text-align: center;
    color: #213945;
    font-size: 11pt;
    line-height: 18pt;
}
.fabricinnersection1 .innerbottom  p {
    text-align: right;
    color: #213945;
    font-size: 11pt;
    line-height: 18pt;
}
@media (max-width: 1024px){
.fabricdesktopview {
    display: none;
}
.fabricmobileview {
    display: block;
}
.expbottom  p {
    text-align: center;
}
}